<template>
  <div class="list">
    <van-search
      v-model="value"
      placeholder="请输入搜索关键词"
      @search="onSearch"
      class="search"
    />

    <div class="listNav">
      <van-sidebar v-model="activeKey">
        <van-sidebar-item
          @click="onClick(values.id)"
          :title="values.name"
          v-for="values in categories"
          :key="values.id"
        />
      </van-sidebar>

      <div class="concent">
        <van-card
          v-for="item in navCate"
          :key="item.id"
          :num="item.amount"
          :price="item.price.toFixed(2)"
          :title="item.name"
          :thumb="item.coverImage | dalImg"
          @click-thumb="toDetail(item)"
        >
        </van-card>
      </div>
    </div>
  </div>
</template>

<script>
import { loadProductsAPI } from "../services/products";
import { loadCategoriesAPI } from "../services/categories";

export default {
  data() {
    return {
      value: "",
      activeKey: 0,
      page: 1,
      categories: [],
      navCate: [],
    };
  },
  created() {
    this.onClick();
    this.loadCate();
  },
  methods: {
    //点击切换
    async onClick(id) {
      const cateG = await loadProductsAPI(this.page, id);
      this.navCate = cateG.data;
    },

    onSearch(val) {
      this.$router.push({
        name: "search",
        params: {
          content: val,
        },
      });
    },
    toDetail(item) {
      this.$router.push({
        name: "detail",
        params: {
          id: item.id,
        },
      });
    },
    //商品分类
    async loadCate() {
      const resCategories = await loadCategoriesAPI();
      this.categories = resCategories.data;
    },
  },
};
</script>

<style scoped>
.listNav {
  display: flex;
  overflow: auto;
}
.search {
  margin: 10px 0px;
}
.concent {
  flex: 1;
}
</style>
